import { Vazirmatn } from 'next/font/google'
const vazir = Vazirmatn({ subsets: ['latin'] })
import "../globals.css"
export default function layout({ children }: {
    children: React.ReactNode

}) {
    return (
        <html lang="en">
            <body className={vazir.className}>
                {children}
            </body>
        </html>
    )
}
